@import url('https://fonts.cdnfonts.com/css/satoshi');
body {
  background-color: transparent;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Satoshi', sans-serif;
  overflow: hidden;
}
:root {
  --bg: rgba(20, 22, 28, 0.9);
  --lightgray: rgba(255, 255, 255, 0.3);
  --lightlightgray: rgba(255, 255, 255, 0.03);
  --gray: rgba(255, 255, 255, 0.5);
  --darkgray: rgba(255, 255, 255, 0.7);
  --white: rgba(255, 255, 255, 0.9);
  --main-color: #ffffff;
}
.hud-container{
  position: absolute;
  top: 20px;
  right: 20px;
}
.hud{
  background-color: var(--bg);
  height: 40px;
  display: flex;
  align-items: center;
  width: min-content;
  border-radius: 5px;
  float: right;
}
.botright{
  top: unset;
  height: 40px;
  bottom: 20px;
  right: 20px;
}
.icon{
  color: var(--lightgray);
  margin-left: 30px;
  position: relative;
  font-size: 18px;
  display: flex;
}
.icon-overlay{
  width: 19px;
  position: absolute;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  bottom: 0px;
  color: var(--main-color);
}
.icon-back{
  width: 19px;
  display: flex;
  justify-content: center;
}
.icon:last-child{
  margin-right: 30px;
}
.compass{
  background-color: var(--bg);
  height: 40px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  float: right;
  margin-right: 10px;
  font-weight: 900;
  color: var(--darkgray);
  padding: 0 10px 0 10px;
  font-size: 13px;
  align-items: center;
  text-transform: uppercase;
}
.compass i{
  font-size: 18px;
  margin-right: 8px;
}
.notify-container{
  width: min-content;
  float: right;
  position: absolute;
  top: 75px;
  right: 20px;
  height: 800px;
}
.notifybotrightmode{
  top: 20px;
}
.notify{
  width: 306px;
  max-width: 306px;
  min-height: 60px;
  background-color: var(--bg);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}
.notify-icon{
  margin-left: 30px;
  margin-right: 30px;
  color: var(--main-color);
  font-size: 28px;
}
.notify-content{
  display: flex;
  flex-direction: column;
}
.notify-title{
  color: var(--gray);
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: -2px;
}
.notify-description{
  color: var(--white);
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  margin-top: -2px;
}

.transition-enter{
  opacity: 0.01;
  transform: translate(400px, 0px);
}
.transition-enter-active{
  opacity: 1;
  transform: translate(0, 0);
  transition: all 300ms;
}
.transition-exit{
  opacity: 1;
  transform: translate(0, 0);
}
.transition-exit-active{
  opacity: 0.01;
  transform: translate(400px, 0px);
  transition: all 300ms;
}

.progressbar-container{
  width: 100%;
  position: absolute;
  margin-top: 20px;
  opacity: 0;
  transition: 0.1s;
  top: 0;
}
.progressbar{
  width: 460px;
  min-height: 40px;
  height: min-content;
  border-radius: 5px;
  background-color: var(--bg);
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.progressbar-content{
  padding: 12px 0 16px 0;
}
.progressbar-title{
  font-family: 'Satoshi', sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: var(--darkgray);
}
.progress{
  width: 420px;
  height: 8px;
  margin-top: 4px;
  border-radius: 3px;
  background-color: rgba(255,255,255, 0.03);
  border: 1px solid rgba(255,255,255, 0.3);
}
.bar{
  width: 0%;
  height: 100%;
  border-radius: 2px;
  background-color: var(--darkgray);
}
.stop-bar{
  width: 100%;
}
.carhud{
  width: 14.0104vw;
  bottom: 3.2407vh;
  left: 1.5625vw;
  position: absolute;
  opacity: 0;
  transition: 0.1s;
}
.map{
  height: 15.74075vh;
  width: 100%;
  background-color: rgba(172, 155, 155, 0);
  position: relative;
}
.wide{
  left: 14.8255vw;
}
.carhud-container{
  display: flex;
  margin-bottom: 10px;
}
.carhud-seatbelt{
  width: 100%;
  display: flex;
  margin-bottom: 6px;
  height: 25px;
  font-size: 11px;
}
.carhud-seatbelt-content{
  height: 100%;
  border-radius: 5px;
  background-color: var(--bg);
  width: 100%;
  display: flex;
  font-weight: 700;
  align-items: center;
  justify-content: center;
  color: var(--darkgray)
}
.carhud-fuel{
  width: 40px;
  height: 40px;
  margin-right: 10px;
  background-color: var(--bg);
  border-radius: 5px;
  flex-basis: 40px;
  flex-grow: 0;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.carhud-fuel .icon{
  font-size: 14px;
  margin: 0;
}
.carhud-main{
  border-radius: 5px;
  background-color: var(--bg);
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.carhud-speed{
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 900;
  font-size: 18px;
  line-height: 14px;
  color: var(--main-color)
}
.carhud-speed-unit{
  color: var(--darkgray);
  font-size: 10px;
  font-weight: 900;
  font-size: 12px;
  line-height: 9px;
  margin-right: 15px;
  margin-left: 5px;
}
.carhud-streetlabel{
  height: 100%;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
}
.carhud-street{
  font-weight: 700;
  font-size: 11px;
  line-height: 8px;
  color: var(--darkgray);
  max-width: 156px;
  white-space: nowrap;
}
.carhud-street:first-child{
  margin-top: 9px;
  margin-bottom: 6px;
}
.carhud-streetlabel-nocrossroad{
  justify-content: center;
}
.carhud-street-nocrossroad{
  margin: 0 !important;
  font-size: 14px;
}
.talking{
  filter: drop-shadow(0px 0px 4px var(--main-color));
}
.test2{
  position: absolute;
  top: 20px;
}
.test3{
  position:absolute;
  top: 60px;
}
.settings-container{
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  transition: 0.1s;
}
.settings-hud-position{
  display: flex;
  color: #000;
  font-weight: 700;
  align-items: center;
  width: 600px;
  justify-content: space-between;
  transform-origin: center center;
  position: absolute;
  top: 50%;
  right: -10%;
  transform: rotate(90deg) translateY(-50%);
  color: var(--main-color);
  user-select: none;
}
.settings-hud-position-line{
  width: 200px;
  height: 5px;
  background-color: var(--bg);
  cursor: pointer;
  transition: 0.1s;
}
.settings-hud-position-line.position-active{
  background-color: var(--main-color);
}
.settings-hud-position-line:hover{
  background-color: var(--main-color);
}
.settings-header{
  height: 60px;
  width: 800px;
  background-color: var(--bg);
  color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 18px;
  font-weight: 900;
  border-radius: 5px;
  margin-bottom: 20px;
}
.settings-header-exit{
  position: absolute;
  right: 40px;
  font-size: 20px;
  color: var(--darkgray);
  transition: 0.1s;
  cursor: pointer;
}
.settings-header-exit:hover{
  color: var(--white);
}
.settings-settings{
  background-color: var(--bg);
  width: 800px;
  padding: 40px;
  box-sizing: border-box;
  border-radius: 5px;
  max-height: 500px;
  overflow-y: scroll;
}
.settings-settings::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.settings-settings {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.setting{
  width: 100%;
  height: 50px;
  background: var(--lightlightgray);
  border: 1px solid var(--lightgray);
  border-radius: 5px;
  padding: 0 20px 0 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  margin-bottom: 14px;
  user-select: none;
}
.setting:hover .setting-switch{
  color: var(--white);
}
.setting-title{
  font-weight: 900;
  font-size: 16px;
  color: var(--white);
  text-transform: uppercase;
}
.setting-switch{
  font-weight: 700;
  font-size: 16px;
  color: var(--darkgray);
  transition: 0.1s;
}
.setting:last-child{
  margin-bottom: 0;
}
.cinema{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.cinema-bar{
  width: 100%;
  background-color: #000;
  height: 10vh;
}
.notvis{
  opacity: 0;
  transition: 0.1s;
}
.visible{
  opacity: 1;
}

.setting-color{
  display: block;
  height: auto;
}
.setting-color .setting-title{
  margin: 14px 0 14px 0;
}

.colorpicker{
  height: 300px;
}

.ReactColorPicker-module_react_color_picker__8cFXI .ReactColorPicker-module_saturation_value_selector__WPqBq {
  flex: none !important;
  margin-bottom: 10px !important;
  height: 200px !important;
  width: 100% !important;
}
.SaturationValueSelector-module_saturation_value_selector__DwmP7{
  border-radius: 5px;
  margin-left: 0 !important;
  margin-right: 0 !important;
}
