  position: relative;
  height: 18px !important;
  margin: 0 !important;
  border-radius: 5px;
}
.ReactColorPicker-module_react_color_picker__8cFXI .ReactColorPicker-module_details__mF5Se {
  margin: 0 !important;
}
.ReactColorPicker-module_react_color_picker__8cFXI .ReactColorPicker-module_details__mF5Se label span {
  display: none;
}
.ReactColorPicker-module_react_color_picker__8cFXI .ReactColorPicker-module_details__mF5Se label {
  width: 100%;
}
.ReactColorPicker-module_hex_value__iYcr2 {
  margin-left: 0 !important;
  width: 100% !important;
  box-sizing: border-box !important;
  background-color: transparent;
