  padding: 10px 0;
  border: 1px solid var(--lightgray);
  color: #fff;
  text-align: center;
  font-family: 'Satoshi', sans-serif;
  font-weight: 700;
  font-size: 16px;
  outline: none !important;
  margin-top: 10px !important;
}
.radio-container{
  background-color: var(--bg);
  min-width: 160px;
  width: min-content;
  max-width: 300px;
  color: var(--darkgray);
  font-family: 'Satoshi', sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 900;
  display: flex;
  flex-direction: column;
  padding: 14px;
  box-sizing: border-box;
  gap: 5px;
  border-radius: 5px;
  transform-origin: center left;
}
.radio-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 7px;
}
.radio-player{
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.1s;
}
.radio-body{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  padding: 25px;
  box-sizing: border-box;
}
.player-speech{
  color: var(--white);
}

